
import React, { useMemo } from 'react';
import { Tabs, Tab } from '@mui/material';
import PostCardColumn from '../post-card-column';
import './style.scss';

function PostTabs({ tabIndex = 0, onChange, tabs = [], posts = [], showMoreButton }) {
    // 안전한 배열과 인덱스 검증
    const safeTabs = Array.isArray(tabs) ? tabs : [];
    const safeTabIndex = typeof tabIndex === 'number' && tabIndex >= 0 && tabIndex < safeTabs.length ? tabIndex : 0;
    const safePosts = Array.isArray(posts) ? posts : [];

    const tabPosts = useMemo(() => {
        // tabs 배열이 비어있거나 유효하지 않은 경우
        if (safeTabs.length === 0) return safePosts;

        // tabIndex가 유효하지 않은 경우
        if (!safeTabs[safeTabIndex]) return safePosts;

        // 'All' 카테고리인 경우 모든 포스트 반환
        if (safeTabs[safeTabIndex] === 'All') return safePosts;

        // 특정 카테고리 필터링
        return safePosts.filter((post) => {
            if (!post || !Array.isArray(post.categories)) return false;
            return post.categories.includes(safeTabs[safeTabIndex]);
        });
    }, [safePosts, safeTabs, safeTabIndex]);

    // tabs가 비어있는 경우 기본 렌더링
    if (safeTabs.length === 0) {
        return (
            <div className="post-tabs-wrapper">
                <PostCardColumn
                    posts={showMoreButton ? safePosts.slice(0, 4) : safePosts}
                    showMoreButton={showMoreButton && safePosts.length > 4}
                    moreUrl="posts/"
                />
            </div>
        );
    }

    return (
        <div className="post-tabs-wrapper">
            <div className="post-tabs">
                <Tabs
                    className="mui-tabs"
                    value={safeTabIndex}
                    onChange={onChange}
                    variant="scrollable"
                    scrollButtons="auto"
                >
                    {safeTabs.map((title, index) => (
                        <Tab label={title || `Tab ${index + 1}`} key={index} />
                    ))}
                </Tabs>
            </div>
            <PostCardColumn
                posts={showMoreButton ? tabPosts.slice(0, 4) : tabPosts}
                showMoreButton={showMoreButton && tabPosts.length > 4}
                moreUrl={`posts/${safeTabIndex === 0 ? '' : safeTabs[safeTabIndex] || ''}`}
            />
        </div>
    );
}

export default PostTabs;