import React from 'react';
import Layout from '../layout';
import Seo from '../components/seo';
import PostCardColumn from '../components/post-card-column';
import PostTabs from '../components/post-tabs';
import Post from '../models/post';

function CategoryTemplate({ pageContext }) {
    // pageContext 검증
    if (!pageContext) {
        return (
            <Layout>
                <Seo title="Posts" />
                <div>카테고리를 찾을 수 없습니다.</div>
            </Layout>
        );
    }

    const { edges = [], currentCategory = 'All', categories = ['All'] } = pageContext;

    // edges가 배열이 아닌 경우 방어
    const safeEdges = Array.isArray(edges) ? edges : [];

    const posts = safeEdges
        .filter(({ node }) => node && node.frontmatter) // null/undefined 필터링
        .map(({ node }) => new Post(node));

    // 현재 카테고리의 인덱스 찾기
    const currentTabIndex = categories.findIndex(cat => cat === currentCategory);
    const safeTabIndex = currentTabIndex >= 0 ? currentTabIndex : 0;

    return (
        <Layout>
            <Seo title="Posts" />
            <PostTabs
                pageContext={pageContext}
                tabIndex={safeTabIndex}
                tabs={categories}
                posts={posts}
                showMoreButton={false}
            />
        </Layout>
    );
}

export default CategoryTemplate;